<template>
    <div class="qiong-td qiong-td-h-full qiong-line-hight-15">

        <div class="qiong-wide-11">{{ _item.number_code }}</div>
        <div class="qiong-wide-13">{{ view.if_def(_item.sponser_id, 'yourself') }}</div>
        <div class="qiong-wide-11">{{ index }}</div>
        <div class="qiong-wide-12">0</div>
        <div class="qiong-wide-11">{{ _item.ipv }}</div>
        <div class="qiong-wide-10">
            <level-viewing :item="_item.vip.name + '_' + _item.vip.star"></level-viewing>
        </div>
        <div class="qiong-wide-13 text-center">{{ view.if_def(usd_ratio) }}</div>
        <div class="qiong-wide-11 pr-0">{{ usd }}</div>
        <div class="qiong-wide-8 pr-0">
            <qiong-icon-inset :color="''" 
                v-if="_item.index != 0 && _item.has_child"
                :arg="_item" @done_Father="openCenter"
            ></qiong-icon-inset>
        </div>
    </div>
</template>

<script>
import QiongIconInset from '../../../../components/Qiong/Button/QiongIconInset.vue'
import LevelViewing from '../../../../components/Viewing/LevelViewing.vue'
    export default {
        components: { QiongIconInset, LevelViewing },
        inject: [ 'reload' ],
        props: {
            _item: {
                type: Object
            },
            me: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                ratios: [ ]
            }
        },
        created() {
            // this.ratios = this.bonus.conf.RECOMMEND_RATIOS
        },
        computed: {
            index() {
                return this._item.index - this.me.index
            },

            usd_ratio() {
                if (this._item.vip.ratio.length > 0) {
                    let res = this._item.vip.ratio[ this._item.index - this.me.index - 1 ]
                    if (res) {
                        return this.view.floatMul(res,  100) + '%'
                    }
                }
                return ''
            },
            usd() {
                if (this._item.vip.ratio.length > 0) {
                    let res = this._item.vip.ratio[ this._item.index - this.me.index - 1 ]

                    if (this._item.unid == this.me.unid) {
                        return this.me.bonus.usd_team_layer
                    }
                    if (res) {
                        return this.view.floatMul(res,  this._item.ipv)
                    }
                }
                return '0'
            }
        },
        mounted() {
            
        },
        methods: {
            openCenter(item) {
                // /home/user/sponser


                this.$router.push({
                    path: '/home/user/sponser', query: { 'id': item.unid } })

                this.reload()
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


